<template>
    <article class="article-wrapper article4">
        <h1 class="title fade10" :class="{'active': $parent.isfadeInActive && $parent.isfadeInA4}"
        v-text="data.title"></h1>
        <div class="info-wrapper fade11"
        :class="{'active': $parent.isfadeInActive && $parent.isfadeInA4}">
            <p class="source">
                {{data.title_unit}}
                <a href="" title="FB" class="fb-btn"
                @click.prevent="$parent.onShare('fb', data.share_link);
                gaEvent('share_fb', 'lost_sharefb');">
                    <img src="@/assets/images/btn_article_fb_n@3x.png" />
                </a>
                <a href="" title="Line" class="line-btn"
                @click.prevent="$parent.onShare('line', data.share_link_line, data.line_text);
                gaEvent('share_line', 'lost_shareline');">
                    <img src="@/assets/images/btn_article_line_n@3x.png" />
                </a>
            </p>
        </div>
        <div class="cols-wrapper fade12"
        :class="{'active': $parent.isfadeInActive && $parent.isfadeInA4}">
            <div class="photo-wrapper"
            :style="{'background-image':
            `url('${(data.images && data.images.length) ? data.images[0] : ''}')`}">
                <img id="article4Photo" :src="data.images[1]"
                class="photo" v-if="data.images && data.images.length && data.images[1]"/>
                <img src="@/assets/images/article4/img_article4_deer@2x.png"
                class="photo photo-placeholder" v-else/>
            </div>
            <p class="description" v-html="data.content">
                
                
                76920
            </p>
        </div>
    </article>
</template>

<script>
import mixinMethod from '@/js/mixin/mixinMethod';

export default {
    name: 'report4',
    props: {
        data: {
            type: Object,
            default() {
                return {
                    title: '',
                    title_unit: '',
                    content: '',
                    images: [],
                    share_link: '',
                };
            },
        },
    },
    data() {
        return {
            gaCategory: '',
        };
    },
    mounted() {
    },
    methods: {
    },
    watch: {
    },
    mixins: [mixinMethod],
};
</script>
